.header {
  font-family: 'Inter', 'Arial', 'Sans-serif';
  max-width: 880px;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  padding-bottom: 41px;
  width: 100%;
}

@media screen and (max-width: 320px) {
  .header {
    max-width: 320px;
    padding-bottom: 32px;
  }
}
